<template>
  <div class="container" :style="{'--second': warna.subtext}">
    <div class="wraper_name">
     <div class="back-btn">
      <backBtn @click="$router.go(-1)" />
     </div>    
      <form >
         <inputText :warna="warna" idName="email" types="email" textLabel="Email baru anda" placeholder="Masukan email baru anda" @data-input="handleDepan"/> 
       <inputText :warna="warna" idName="password" types="password" textLabel="Masukan Password" placeholder="Masukan password anda" @data-input="handlePW"/> 
        <inputText :warna="warna" textLabel="Kode verifikasi" idName="verifikasi" placeholder="Masukan kode verifikasi" verifikasi="bottom"  @data-input="handleBelakang"/>
      </form>
    <p class="waitText" v-if="sended">
      Kirim ulang dalam {{ detik }}s 
    </p>
    <p class="verifText" :style="{ '--primary': warna.primary }" @click="kirimVerif" v-else>
      Tidak ada kode verifikasi?
    </p>
    </div>
    <p class="danger-text">{{ pesan }}</p>
     <sendBtn :warna="warna.primary" v-if="active" @click="handleClick"/>
     <sendBtn :warna="warna.text" v-else @click="handleGagal"/>
  </div>
</template>

<script>
import inputText from '../components/etc/inputText.vue'
/*{
idName,{{ pesan }}
textLabel,
warna,
placeholder
}*/
import backBtn from '../components/mats/backBtn.vue'
import sendBtn from '../components/mats/sendBtn.vue'

  export default{
    name: 'changeEmail',
    data() {
      return {
        email: '',
        verifikasi: '',
        password: '',
        verif: '273',
        active: false,
        pesan: '',
        detik: 60,
        sended: false
      }
    },
    components: {
      inputText,
      backBtn,
      sendBtn
    },
    methods: {
      kirimVerif() {
        this.sended = !this.sended;
        const time = setInterval(() => {
          this.detik -= 1;
          if(this.detik < 0) {
            this.sended = !this.sended;
            clearInterval(time);
            this.detik = 60;
          }
        }, 1000)
        this.$swal({
          title: 'Berhasil',
          text: 'Kode verifikasi berhasil di kirim, silahkan cek email anda jika tidak ada silahkan cek folder spam',
          icon: 'success'
        })
      },
      handleGagal() {
        this.$notify({
          title: 'Gagal',
          text: 'Pastikan kode verifikasi dan email sudah benar!',
          type: 'error'
        })
      },
      handleDepan(data) {
        this.email = data;
        this.validatornya();
      },
      handleBelakang(data) {
        this.verifikasi = data;
        this.validatornya();
      },
      handlePW(data){
        this.password = data;
        this.validatornya();
      },
      validatornya() {
       /** if(this.verifikasi === this.verif ){
          this.active = true;
          this.pesan = ''
        } else {
          this.active = false;
          this.pesan = 'Pastikan kode verifikasi anda benar!'
        }**/
        this.active = true;
      },
      handleClick() {
        this.$swal.fire({
          icon: 'info',
          title: 'Yakin?',
          text: 'Email anda akan di ganti, setelah ganti email anda tidak akan bisa ganti nama selama 2 bulan',
          showCancelButton: true,
          cancelButtonText: 'Tidak'
        }).then(async r => {
          if(r.isConfirmed) {
            try {
              const res = await this.axios.put('/Authentification/changeEmail', {
                email: this.email,
                password: this.password
              }, {
                headers: {
                  authorization: 'Bearer ' + localStorage.getItem('xtoken')
                }
              })
              alert(JSON.stringify(res));
            } catch (e) {
              alert(e);
            }
          }
        })
      }
    },
    props: {
      warna: {
        type: Object,
        required: true
      }
    },
    created() {
      this.$emit('hilang-navbar', false)
    }
  }
</script>

<style scoped>
.container {
  position: relative;
  width: 100%;
  padding: 0.3rem 0.5rem;
}
.wraper_name {
  max-width: 800px;
  margin: 0 auto;
}
.danger-text {
  color: #a32222;
  font-size: 0.6rem;
  letter-spacing: 0.4px;
  line-height: 140%;
}
.verifText {
  color: var(--primary);
  font-size: 0.7rem;
}
.waitText {
  color: #999;
  font-size: 0.6rem;
}
</style>
